.player {
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.time-control {
    width: 50%;
    display: flex;

    input{
        width: 100%;
        // padding: 1rem 0rem;
        cursor: pointer;
        -webkit-appearance: none;
        background: transparent;
    }
    p{
        padding: 0rem 1rem;
        height: 1rem;
    }
}

.play-control{
    width: 30%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;

    svg {
        cursor: pointer;
    }
}

input[type="range"]:focus {
    outline: none;
}

input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 16px;
    width: 16px;
}

input[type="range"]::-moz-range-thumb {
    -moz-appearance: none;
    height: 16px;
    width: 16px;
}

.track {
    background: lightblue;
    width: 100%;
    height: 1rem;
    position: relative;
    border-radius: 1rem;
    overflow: hidden;
}

.animate-track {
    background: rgb(204, 204, 204);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(0%);
    pointer-events: none;
}

@media screen and (max-width: 768px) {
    .time-control{
        width: 90%;
    }
    .play-control{
        width: 70%;
    }
}